<template>
  <div class="main-box-admin">
    <el-breadcrumb separator="/" style="font-size: 18px;">
      <el-breadcrumb-item :to="{ path: '/Tan/ManageCarbon' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <br>
    <div>
      <div class="table_container" style="width: 100%; overflow-x: auto; overflow-y: visible;">
        <el-card shadow="never" style="border-radius: 12px;">
          <div slot="header" class="clearfix" style="display: flex; justify-content: space-between; align-items: center;">
            <span style="font-size: 18px; font-weight: bold; color: white;">
              <i class="el-icon-school" style="margin-right: 8px;"></i>
            </span>
            <el-button style="padding: 3px 0; color: white;" type="text" @click="loadSchoolInfo" icon="el-icon-refresh"></el-button>
          </div>
          <el-form :model="schoolData" ref="schoolForm" label-width="160px" :rules="schoolRules" style="padding: 20px;">
            <el-form-item label="" prop="schoolName">
              <el-input v-model="schoolData.schoolName" style="width: 750px" placeholder="" class="text-center-input"></el-input>
            </el-form-item>
            <el-form-item label="" prop="totalNumber">
              <el-input-number v-model="schoolData.totalNumber" :min="0" style="width: 750px" placeholder=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="studentNumber">
              <el-input-number v-model="schoolData.studentNumber" :min="0" style="width: 750px" placeholder=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="teacherNumber">
              <el-input-number v-model="schoolData.teacherNumber" :min="0" style="width: 750px" placeholder=""></el-input-number>
            </el-form-item>
            <el-form-item label="(m)" prop="totalArea">
              <el-input-number v-model="schoolData.totalArea" :min="0" style="width: 750px" placeholder=""></el-input-number>
            </el-form-item>
            <el-form-item label="(m)" prop="buildingArea">
              <el-input-number v-model="schoolData.buildingArea" :min="0" style="width: 750px" placeholder=""></el-input-number>
            </el-form-item>
            <el-form-item label="(m)" prop="greenArea">
              <el-input-number v-model="schoolData.greenArea" :min="0" style="width: 750px" placeholder=""></el-input-number>
            </el-form-item>
            <el-form-item label="" prop="imageUrl">
              <el-input v-model="schoolData.imageUrl" style="width: 750px" placeholder="URL" class="text-center-input"></el-input>
              <div style="margin-top: 10px;margin-left: 20px; color: #909399; font-size: 12px;">
                URLhttps://example.com/image.jpg  /static/images/school.jpg
              </div>
            </el-form-item>
            <el-form-item class="form-actions">
              <el-button style="margin-left: 20px;" type="primary" @click="updateSchoolInfo" :loading="updating"></el-button>
              <el-button @click="loadSchoolInfo"></el-button>
            </el-form-item>
          </el-form>
        </el-card>
      </div>
    </div>
  </div>
</template>
<script>
import { getSchoolInfo, updateSchoolInfo } from "../../api/school";
import { getCurrentUserRoles } from "../../api/userManage";
import { extractPermissionsFromRoles, hasPermission } from "../../utils/permission";
export default {
  data() {
    return {
      schoolData: {
        id: null,
        schoolName: '',
        totalNumber: null,
        studentNumber: null,
        teacherNumber: null,
        totalArea: null,
        buildingArea: null,
        greenArea: null,
        imageUrl: ''
      },
      originalSchoolData: {},
      updating: false,
      userPermissions: [], // 
      schoolRules: {
        schoolName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        totalNumber: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 0, message: '0', trigger: 'blur' }
        ],
        studentNumber: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 0, message: '0', trigger: 'blur' }
        ],
        teacherNumber: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 0, message: '0', trigger: 'blur' }
        ],
        totalArea: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 0, message: '0', trigger: 'blur' }
        ],
        buildingArea: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 0, message: '0', trigger: 'blur' }
        ],
        greenArea: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'number', min: 0, message: '0', trigger: 'blur' }
        ]
      }
    }
  },
  mounted: function () {
    this.loadUserPermissions()
    this.loadSchoolInfo()
  },
  methods: {
    // 
    loadUserPermissions() {
      getCurrentUserRoles().then(res => {
        if (res.data.code == 200) {
          const roles = res.data.data || [];
          this.userPermissions = extractPermissionsFromRoles(roles);
        }
      }).catch(() => {
        this.userPermissions = [];
      });
    },
    // 
    checkPermission(permissionCode) {
      return hasPermission(this.userPermissions, permissionCode);
    },
    loadSchoolInfo() {
      getSchoolInfo().then(res => {
        if (res.data.code == 200) {
          this.schoolData = res.data.data || {}
          // 
          this.originalSchoolData = JSON.parse(JSON.stringify(this.schoolData))
        } else {
          this.$message.error(res.data.message || "");
        }
      }).catch((error) => {
        // 40101request.js
        if (!error.response || !error.response.data || error.response.data.code !== 40101) {
          this.$message.error("");
        }
      });
    },
    updateSchoolInfo() {
      this.$refs.schoolForm.validate((valid) => {
        if (valid) {
          this.updating = true
          updateSchoolInfo(this.schoolData).then(res => {
            if (res.data.code == 200) {
              this.$message.success("")
              this.loadSchoolInfo()
            } else {
              this.$message.error(res.data.message || "")
            }
          }).catch((error) => {
            // 40101request.js
            if (!error.response || !error.response.data || error.response.data.code !== 40101) {
              this.$message.error("")
            }
          }).finally(() => {
            this.updating = false
          });
        }
      });
    }
  }
}
</script>

<style scoped>
/* .main-box-admin  common.css  */

.table_container {
  width: 100%;
  overflow-x: auto;
  overflow-y: visible;
}

/*  */
::v-deep .el-form-item__label {
  margin-right: 20px !important; /*  */
}

/*  */
.form-actions ::v-deep .el-form-item__content {
  margin-left: 160px !important; /* 160px */
}

/*  -  */
/* Vue 2  /deep/  */
/deep/ .el-input__inner {
  text-align: center !important;
}
</style>




