<template>
  <div class="container">
    <!-- style-->
    <div :id="ids" :style="{ width: '100%', height: '100%' }"></div>
  </div>
</template>
<!--, border: '1px solid #000'-->
<script>
export default {
  props: ["x", "y", "ids"],
  mounted() {
    this.drawLine(this.y);
  },
  methods: {
    drawLine(value) {
      let haveValueFlag = false;
      for (let i = 0; i < value.length; i++) {
        var tempValue = value[i].value;
        if (tempValue == 0) {
          value[i].value = null;
        } else {
          haveValueFlag = true;
        }
      }

      this.y = value;
      //  DOM 
      const chartDom = document.getElementById(this.ids);
      if (!chartDom) {
        return;
      }
      
      // 
      let myChart = this.$echarts.getInstanceByDom(chartDom);
      if (myChart) {
        myChart.dispose();
      }
      
      // domecharts
      myChart = this.$echarts.init(chartDom);
      // 
      var haveValueOption = {
        legend: {
          orient: 'vertical',
          right: '10%',
          top: 'middle',
          data: this.x,
          textStyle: {
            fontSize: 14
          },
          itemWidth: 14,
          itemHeight: 14,
          itemGap: 10
        },
        tooltip: {
          trigger: 'item',
          formatter: (params) => {
            // console.log(params);
            return '' + '<br/>' + params.marker + params.name + ':' + parseFloat(params.value).toFixed(2) + '(kg)' + '<br/>';
          },
          show: true,
        },
        xAxis: {
          type: "category",
          show: false,
        },
        yAxis: {
          type: "value",

        },
        series: [{
          name: '',
          type: 'pie',
          radius: ['45%', '75%'],
          center: ['35%', '50%'],
          barWidth: '10%',
          data: value,
          label: {
            show: true,
            position: 'outside',
            formatter: `{d}%`,
            fontSize: 12
          },
          labelLine: {
            length: 15,
            length2: 10
          }
        }]
      };

      myChart.setOption(haveValueOption);

    },
  },
};
</script>
<style scoped>
#a,
#b {
  float: left;
  margin: 0px;
}
</style>



