<template>
  <div class="main-box-admin">
    <el-breadcrumb separator="/" style="font-size: 18px; margin-bottom: 20px;">
      <el-breadcrumb-item :to="{ path: '/Tan/TanPage' }">
        <i class="el-icon-s-home"></i> 
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="forest-card" style="padding: 20px; margin-bottom: 20px;">
      <div class="flex-box-header-new">
        <div class="energyleft">
          <el-form>
            <el-tag style="font-size: 13px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="searchRoleCode" placeholder="" size="small" style="width:150px"></el-input>
            </el-form-item>
            <el-tag style="font-size: 13px; margin-left: 10px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="searchRoleName" placeholder="" size="small" style="width:150px"></el-input>
            </el-form-item>
            <el-button class="form-item-inline" size="small" @click="getRoleList" plain
              icon="el-icon-search"></el-button>
            <el-button class="form-item-inline" type="success" size="small" @click="getAllRoleList" plain
              icon="el-icon-search"></el-button>
          </el-form>
        </div>
        <div style="margin-left: 50px;margin-bottom: 21px;">
          <el-form>
            <el-button class="form-item-inline" type="primary" size="small" @click="showAddDialog"></el-button>
          </el-form>
        </div>
      </div>
      <div class="table_container" style="width: 100%; overflow-x: auto; overflow-y: visible;">
        <el-table :data="tableData" :key="tableKey" style="width: 100%;font-size: 15px;" size="small" class="forest-table">
          <el-table-column label="" min-width="80" align="center">
            <template slot-scope="scope">
              {{ (scope.$index + 1) + (current - 1) * limit }}
            </template>
          </el-table-column>
          <el-table-column prop="roleCode" label="" min-width="150" align="center"></el-table-column>
          <el-table-column prop="roleName" label="" min-width="150" align="center"></el-table-column>
          <el-table-column prop="description" label="" min-width="250" align="center"></el-table-column>
          <el-table-column prop="status" label="" min-width="100" align="center">
            <template slot-scope="scope">
              <el-tag :type="scope.row.status === 1 ? 'success' : 'info'" size="small">
                {{ scope.row.status === 1 ? '' : '' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="createdTime" label="" min-width="180" align="center"></el-table-column>
          <el-table-column label="" min-width="320" fixed="right" align="center">
            <template slot-scope="scope">
              <el-button @click="showUpdateDialog(scope.row)" type="warning" plain size="mini" icon="el-icon-edit" style="margin: 2px;"></el-button>
              <el-button @click="showAssignPermissionDialog(scope.row)" type="primary" plain size="mini" icon="el-icon-key" style="margin: 2px;"></el-button>
              <el-button @click="deleteRoleById(scope.row)" type="danger" plain size="mini" icon="el-icon-delete" style="margin: 2px;"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="width: 100%; text-align:center; margin:20px;padding-right: 50px;">
          <el-pagination background @current-change="handleCurrentChange" :page-size="limit" :current-page.sync="current"
            layout="total, prev, pager, next" :total="total" v-if="pageshow">
          </el-pagination>
        </div>
      </div>

      <!--  -->
      <el-dialog title="" :visible.sync="addShow" :modal="false" top="10%" width="50%">
        <el-form :model="addRoleData" ref="addRoleForm" label-width="120px" :rules="roleRules">
          <el-form-item label="" prop="roleCode">
            <el-input v-model="addRoleData.roleCode" style="width: 300px" placeholder="ADMIN"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleName">
            <el-input v-model="addRoleData.roleName" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input v-model="addRoleData.description" type="textarea" :rows="3" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="addRoleData.status" placeholder="" style="width: 300px">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="insertRoleData"></el-button>
            <el-button @click="addShow = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="updateShow" :modal="false" top="10%" width="50%">
        <el-form :model="updateRoleData" ref="updateRoleForm" label-width="120px" :rules="roleRules">
          <el-form-item label="" prop="roleCode">
            <el-input v-model="updateRoleData.roleCode" style="width: 300px" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleName">
            <el-input v-model="updateRoleData.roleName" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input v-model="updateRoleData.description" type="textarea" :rows="3" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="updateRoleData.status" placeholder="" style="width: 300px">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="updateRoleDataById"></el-button>
            <el-button @click="updateShow = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="assignPermissionShow" :modal="false" top="5%" width="60%" @opened="handleAssignPermissionDialogOpened">
        <el-form label-width="120px">
          <el-form-item label="">
            <el-input v-model="assignPermissionData.roleName" :disabled="true" style="width: 300px"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-tree
              ref="permissionTree"
              :key="permissionTreeKey"
              :data="permissionTreeData"
              show-checkbox
              node-key="id"
              :default-expanded-keys="defaultExpandedKeys"
              :props="{ children: 'children', label: 'permissionName' }"
              :check-strictly="true"
              @check-change="handleCheckChange"
              style="max-height: 400px; overflow-y: auto;">
            </el-tree>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="assignPermissionDataById"></el-button>
            <el-button @click="assignPermissionShow = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getRoleList, addRole, updateRole, deleteRole, assignPermissions, getPermissionTree, getRoleWithPermissions } from "../../api/role";
export default {
  data() {
    return {
      tableKey: 0, // 
      tableData: [],
      current: 1,
      size: 10,
      limit: 10,
      total: 0,
      pageshow: true,
      addShow: false,
      updateShow: false,
      assignPermissionShow: false,

      addRoleData: {
        roleCode: '',
        roleName: '',
        description: '',
        status: 1
      },
      updateRoleData: {},
      assignPermissionData: {},

      searchRoleCode: '',
      searchRoleName: '',

      permissionTreeData: [],
      permissionTreeKey: 0, // 
      defaultExpandedKeys: [], // key

      roleRules: {
        roleCode: [{ required: true, message: '', trigger: 'blur' }],
        roleName: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  mounted: function () {
    this.currentPage = 1
    // getRoleListloadPermissionTree
    this.getRoleList().then(() => {
      // getRoleListloadPermissionTree
      this.loadPermissionTree()
      // 
      this.$nextTick(() => {
        this.tableKey++
      })
    }).catch(() => {
      // getRoleListloadPermissionTree
    })
  },
  activated() {
    // 
    this.getRoleList().then(() => {
      // 
      this.$nextTick(() => {
        this.tableKey++
      })
    })
  },

  methods: {
    loadPermissionTree() {
      getPermissionTree().then(res => {
        if (res.data.code == 200) {
          this.permissionTreeData = res.data.data || []
        } else {
          // 40101request.js
          if (res.data.code !== 40101) {
            this.$message.error(res.data.message || "");
          }
        }
      }).catch((error) => {
        // 40101request.js
        if (!error.response || !error.response.data || error.response.data.code !== 40101) {
          this.$message.error("");
        }
      });
    },
    getAllRoleList() {
      this.searchRoleCode = ""
      this.searchRoleName = ""
      this.getRoleList();
    },
    getRoleList() {
      return getRoleList({
        current: this.current,
        size: this.size,
        roleCode: this.searchRoleCode,
        roleName: this.searchRoleName
      }).then(res => {
        if (res.data.code == 200) {
          this.tableData = res.data.data.records || []
          this.total = res.data.data.total || 0
          return Promise.resolve(res); // 
        } else {
          // 40101request.js
          if (res.data.code !== 40101) {
            this.$message.error(res.data.message || "");
          }
          return Promise.reject(res); // 
        }
      }).catch((error) => {
        // 40101request.js
        if (!error.response || !error.response.data || error.response.data.code !== 40101) {
          this.$message.error("");
        }
        return Promise.reject(error); // 
      });
    },
    handleCurrentChange(val) {
      this.current = val
      this.getRoleList()
    },
    showAddDialog() {
      this.addShow = true;
      this.addRoleData = {
        roleCode: '',
        roleName: '',
        description: '',
        status: 1
      }
      this.$nextTick(() => {
        setTimeout(() => {
          const firstInput = this.$el.querySelector('.el-dialog__body .el-input__inner');
          if (firstInput) {
            firstInput.focus();
          } else {
            const dialogBody = this.$el.querySelector('.el-dialog__body');
            if (dialogBody) {
              dialogBody.setAttribute('tabindex', '-1');
              dialogBody.focus();
            }
          }
        }, 200);
      });
    },
    showUpdateDialog(row) {
      this.updateShow = true;
      this.updateRoleData = {
        id: row.id,
        roleCode: row.roleCode,
        roleName: row.roleName,
        description: row.description,
        status: row.status
      }
    },
    showAssignPermissionDialog(row) {
      // 
      this.assignPermissionData = {
        id: row.id,
        roleName: row.roleName,
        permissionIds: []
      };
      // 
      getRoleWithPermissions({ roleId: row.id }).then(res => {
        if (res.data.code == 200) {
          const roleData = res.data.data;
          this.assignPermissionData = {
            id: roleData.id,
            roleName: roleData.roleName,
            permissionIds: roleData.permissions ? this.flattenPermissionIds(roleData.permissions) : []
          };
          // 
          this.assignPermissionShow = true;
        } else {
          // 40101request.js
          if (res.data.code !== 40101) {
            this.$message.error(res.data.message || "");
          }
        }
      }).catch((error) => {
        // 40101request.js
        if (!error.response || !error.response.data || error.response.data.code !== 40101) {
          this.$message.error("");
        }
      });
    },
    handleAssignPermissionDialogOpened() {
      // 
      // key
      this.defaultExpandedKeys = this.getAllNodeKeys(this.permissionTreeData);
      this.permissionTreeKey += 1;
      this.$nextTick(() => {
        if (this.$refs.permissionTree && this.assignPermissionData.permissionIds) {
          // 
          this.$refs.permissionTree.setCheckedKeys(this.assignPermissionData.permissionIds, true);
        }
      });
    },
    handleCheckChange(data, checked, indeterminate) {
      // 
      const tree = this.$refs.permissionTree;
      if (!tree) return;

      // 
      this.updateParentCheckState(data, tree);
    },
    updateParentCheckState(node, tree) {
      // 
      if (!node.parent) return;

      // 
      const parent = node.parent;
      const children = parent.childNodes;

      // 
      let allChecked = true;
      let anyChecked = false;

      children.forEach(child => {
        if (child.checked) {
          anyChecked = true;
        } else {
          allChecked = false;
        }
      });

      // 
      if (allChecked) {
        tree.setChecked(parent.key, true, false);
      } else {
        tree.setChecked(parent.key, false, false);
      }

      // 
      this.updateParentCheckState(parent, tree);
    },
    getAllNodeKeys(nodes) {
      //  key
      let keys = [];
      if (nodes && nodes.length > 0) {
        nodes.forEach(node => {
          keys.push(node.id);
          if (node.children && node.children.length > 0) {
            keys = keys.concat(this.getAllNodeKeys(node.children));
          }
        });
      }
      return keys;
    },
    flattenPermissionIds(permissions) {
      let ids = [];
      permissions.forEach(perm => {
        if (!perm.children || perm.children.length === 0) {
          ids.push(perm.id);
        }
        if (perm.children && perm.children.length > 0) {
          ids = ids.concat(this.flattenPermissionIds(perm.children));
        }
      });
      return ids;
    },
    insertRoleData() {
      this.$refs.addRoleForm.validate((valid) => {
        if (valid) {
          addRole(this.addRoleData).then(res => {
            if (res.data.code == 200) {
              this.$message.success("")
              this.getRoleList()
              this.addShow = false
            } else {
              this.$message.error(res.data.message || "")
            }
          }).catch(() => {
            this.$message.error("")
          });
        }
      });
    },
    updateRoleDataById() {
      this.$refs.updateRoleForm.validate((valid) => {
        if (valid) {
          updateRole(this.updateRoleData).then(res => {
            if (res.data.code == 200) {
              this.$message.success("")
              this.getRoleList()
              this.updateShow = false
            } else {
              this.$message.error(res.data.message || "")
            }
          }).catch(() => {
            this.$message.error("")
          });
        }
      });
    },
    assignPermissionDataById() {
      // 
      const checkedKeys = this.$refs.permissionTree.getCheckedKeys();
      
      if (checkedKeys.length === 0) {
        this.$message.warning("");
        return;
      }
      
      const assignData = {
        id: this.assignPermissionData.id,
        permissions: checkedKeys.map(permId => ({ id: permId }))
      };
      
      assignPermissions(assignData).then(res => {
        if (res.data.code == 200) {
          this.$message.success("")
          // 
          getRoleWithPermissions({ roleId: this.assignPermissionData.id }).then(roleRes => {
            if (roleRes.data.code == 200) {
              const roleData = roleRes.data.data;
              //  assignPermissionData
              this.assignPermissionData = {
                id: roleData.id,
                roleName: roleData.roleName,
                permissionIds: roleData.permissions ? this.flattenPermissionIds(roleData.permissions) : []
              };
            }
          }).catch(() => {
            // 
          });
          // 
          this.assignPermissionShow = false;
          // 
          this.getRoleList()
        } else {
          this.$message.error(res.data.message || "")
        }
      }).catch(() => {
        this.$message.error("")
      });
    },
    deleteRoleById(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteRole({ id: row.id }).then(res => {
          if (res.data.code == 200) {
            this.$message.success("")
            this.getRoleList()
          } else {
            this.$message.error(res.data.message || "")
          }
        }).catch(() => {
          this.$message.error("")
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
  }
}
</script>

<style scoped>
.cell {
  text-align: center;
}

.energyleft,
.energyright {
  display: inline-block;
}

.energyright {
  vertical-align: top !important;
  margin-bottom: 19px;
}

.el-table__body tr:hover>td {
  background-color: #e8f5e3 !important;
}

.form-item-inline {
  font-size: 13px;
}

.el-table__fixed {
  height: 100% !important;
}

.main-box-admin {
  width: 94%;
  margin: 20px auto 0;
  text-align: left;
  padding: 5px; /* 3%5px */
}

.flex-box-header-new {
  margin-left: 20px;
  margin-right: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: start;
}
</style>




