<template>
  <div class="main-box-admin">
    <el-breadcrumb separator="/" style="font-size: 18px; margin-bottom: 20px;">
      <el-breadcrumb-item :to="{ path: '/Tan/TanPage' }">
        <i class="el-icon-s-home"></i> 
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="forest-card" style="padding: 20px; margin-bottom: 20px;">
      <div class="flex-box-header-new">
        <div class="energyleft">
          <el-form>
            <el-tag style="font-size: 13px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="searchName" placeholder="" size="small" style="width:150px"></el-input>
            </el-form-item>
            <el-tag style="font-size: 13px; margin-left: 10px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="searchUsername" placeholder="" size="small" style="width:150px"></el-input>
            </el-form-item>
            <el-tag style="font-size: 13px; margin-left: 10px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="searchDepartment" placeholder="" size="small" style="width:150px"></el-input>
            </el-form-item>
            <el-button class="form-item-inline" size="small" @click="getUserList" plain
              icon="el-icon-search"></el-button>
            <el-button class="form-item-inline" type="success" size="small" @click="getAllUserList" plain
              icon="el-icon-search"></el-button>
          </el-form>
        </div>
        <div style="margin-left: 50px;margin-bottom: 21px;">
          <el-form>
            <el-button class="form-item-inline" type="primary" size="small" @click="showAddDialog"></el-button>
          </el-form>
        </div>
      </div>
      <div class="table_container" style="width: 100%; overflow-x: auto; overflow-y: visible;">
        <el-table :data="tableData" :key="tableKey" style="width: 100%;font-size: 15px;" size="small" class="forest-table">
          <el-table-column label="" min-width="80" align="center">
            <template slot-scope="scope">
              {{ (scope.$index + 1) + (current - 1) * limit }}
            </template>
          </el-table-column>
          <el-table-column prop="name" label="" min-width="120" align="center"></el-table-column>
          <el-table-column prop="username" label="" min-width="150" align="center"></el-table-column>
          <el-table-column prop="department" label="" min-width="150" align="center"></el-table-column>
          <el-table-column prop="phone" label="" min-width="150" align="center"></el-table-column>
          <el-table-column prop="status" label="" min-width="100" align="center">
            <template slot-scope="scope">
              <el-tag :type="scope.row.status === 1 ? 'success' : 'info'" size="small">
                {{ scope.row.status === 1 ? '' : '' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="roles" label="" min-width="200" align="center">
            <template slot-scope="scope">
              <el-tag v-for="role in scope.row.roles" :key="role.id" size="small" style="margin-right: 5px;">
                {{ role.roleName }}
              </el-tag>
              <span v-if="!scope.row.roles || scope.row.roles.length === 0" style="color: #999;"></span>
            </template>
          </el-table-column>
          <el-table-column prop="createdTime" label="" min-width="180" align="center"></el-table-column>
          <el-table-column label="" min-width="450" fixed="right" align="center">
            <template slot-scope="scope">
              <el-button @click="showUpdateDialog(scope.row)" type="warning" plain size="mini" icon="el-icon-edit" style="margin: 2px;"></el-button>
              <el-button @click="showAssignRoleDialog(scope.row)" type="primary" plain size="mini" icon="el-icon-user" style="margin: 2px;"></el-button>
              <el-button @click="resetUserPassword(scope.row)" type="info" plain size="mini" icon="el-icon-key" style="margin: 2px;"></el-button>
              <el-button @click="deleteUserById(scope.row)" type="danger" plain size="mini" icon="el-icon-delete" style="margin: 2px;"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div style="width: 100%; text-align:center; margin:20px;padding-right: 50px;">
          <el-pagination background @current-change="handleCurrentChange" :page-size="limit" :current-page.sync="current"
            layout="total, prev, pager, next" :total="total" v-if="pageshow">
          </el-pagination>
        </div>
      </div>

      <!--  -->
      <el-dialog title="" :visible.sync="addShow" :modal="false" top="10%" width="50%">
        <el-form :model="addUserData" ref="addUserForm" label-width="120px" :rules="userRules">
          <el-form-item label="" prop="name">
            <el-input v-model="addUserData.name" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="username">
            <el-input v-model="addUserData.username" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="addUserData.password" type="password" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="department">
            <el-input v-model="addUserData.department" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="addUserData.phone" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="addUserData.status" placeholder="" style="width: 300px">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="roleIds">
            <el-select v-model="addUserData.roleIds" multiple placeholder="" style="width: 300px">
              <el-option 
                v-for="role in roleList" 
                :key="role.id" 
                :label="role.roleName" 
                :value="role.id"
                :disabled="isRoleDisabled(role)">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="insertUserData"></el-button>
            <el-button @click="addShow = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="updateShow" :modal="false" top="10%" width="50%">
        <el-form :model="updateUserData" ref="updateUserForm" label-width="120px" :rules="userUpdateRules">
          <el-form-item label="" prop="name">
            <el-input v-model="updateUserData.name" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="username">
            <el-input v-model="updateUserData.username" style="width: 300px" placeholder="" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="department">
            <el-input v-model="updateUserData.department" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="phone">
            <el-input v-model="updateUserData.phone" style="width: 300px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="updateUserData.status" placeholder="" style="width: 300px">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="roles">
            <el-select v-model="updateUserData.roleIds" multiple placeholder="" style="width: 300px">
              <el-option 
                v-for="role in roleList" 
                :key="role.id" 
                :label="role.roleName" 
                :value="role.id"
                :disabled="isRoleDisabled(role)">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="updateUserDataById"></el-button>
            <el-button @click="updateShow = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="assignRoleShow" :modal="false" top="15%" width="40%">
        <el-form label-width="120px">
          <el-form-item label="">
            <el-input v-model="assignRoleData.name" :disabled="true" style="width: 300px"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="assignRoleData.roleIds" multiple placeholder="" style="width: 300px">
              <el-option 
                v-for="role in roleList" 
                :key="role.id" 
                :label="role.roleName" 
                :value="role.id"
                :disabled="isRoleDisabled(role)">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="assignRoleDataById"></el-button>
            <el-button @click="assignRoleShow = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getUserList, addUser, updateUser, deleteUser, resetPassword, assignRoles, getAllRoles, getCurrentUserRoles } from "../../api/userManage";
import { extractPermissionsFromRoles, hasPermission } from "../../utils/permission";
export default {
  data() {
    return {
      tableData: [],
      current: 1,
      size: 10,
      limit: 10,
      total: 0,
      pageshow: true,
      addShow: false,
      updateShow: false,
      assignRoleShow: false,

      addUserData: {
        name: '',
        username: '',
        password: '',
        department: '',
        phone: '',
        status: 1,
        roleIds: []
      },
      updateUserData: {},
      assignRoleData: {},

      searchName: '',
      searchUsername: '',
      searchDepartment: '',

      roleList: [],
      currentUserRoles: [], // 
      currentUserMinOrder: 999, // order
      tableKey: 0, // 
      userPermissions: [], // 

      userRules: {
        name: [{ required: true, message: '', trigger: 'blur' }],
        username: [{ required: true, message: '', trigger: 'blur' }],
        password: [{ required: true, message: '', trigger: 'blur' }]
      },
      userUpdateRules: {
        name: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  mounted: function () {
    this.currentPage = 1
    // 
    this.loadCurrentUserRoles()
    // 
    this.loadUserPermissions()
    // getUserListloadRoleList
    this.getUserList().then(() => {
      // getUserListloadRoleList
      this.loadRoleList()
      // 
      this.$nextTick(() => {
        this.tableKey++
      })
    }).catch(() => {
      // getUserListloadRoleList
    })
  },
  activated() {
    // 
    this.getUserList().then(() => {
      // 
      this.$nextTick(() => {
        this.tableKey++
      })
    })
  },

  methods: {
    loadRoleList() {
      getAllRoles().then(res => {
        if (res.data.code == 200) {
          this.roleList = res.data.data || []
        } else {
          // 40101request.js
          if (res.data.code !== 40101) {
            this.$message.error(res.data.message || "");
          }
        }
      }).catch((error) => {
        // 40101request.js
        if (!error.response || !error.response.data || error.response.data.code !== 40101) {
          this.$message.error("");
        }
      });
    },
    loadCurrentUserRoles() {
      getCurrentUserRoles().then(res => {
        if (res.data.code == 200) {
          this.currentUserRoles = res.data.data || []
          // order
          if (this.currentUserRoles.length > 0) {
            const orders = this.currentUserRoles
              .map(role => role.order !== null && role.order !== undefined ? role.order : 999)
              .filter(order => order !== null && order !== undefined)
            this.currentUserMinOrder = orders.length > 0 ? Math.min(...orders) : 999
          } else {
            this.currentUserMinOrder = 999
          }
        } else {
          // 40101request.js
          if (res.data.code !== 40101) {
            this.$message.error(res.data.message || "");
          }
        }
      }).catch((error) => {
        // 40101request.js
        if (!error.response || !error.response.data || error.response.data.code !== 40101) {
          this.$message.error("");
        }
      });
    },
    // 
    loadUserPermissions() {
      getCurrentUserRoles().then(res => {
        if (res.data.code == 200) {
          const roles = res.data.data || [];
          this.userPermissions = extractPermissionsFromRoles(roles);
        }
      }).catch(() => {
        // 
        this.userPermissions = [];
      });
    },
    // 
    checkPermission(permissionCode) {
      return hasPermission(this.userPermissions, permissionCode);
    },
    // 
    isRoleDisabled(role) {
      // order
      if (role.order === null || role.order === undefined) {
        return false
      }
      // order < order
      return role.order < this.currentUserMinOrder
    },
    getAllUserList() {
      this.searchName = ""
      this.searchUsername = ""
      this.searchDepartment = ""
      this.getUserList();
    },
    getUserList() {
      return getUserList({
        current: this.current,
        size: this.size,
        name: this.searchName,
        username: this.searchUsername,
        department: this.searchDepartment
      }).then(res => {
        if (res.data.code == 200) {
          this.tableData = res.data.data.records || []
          this.total = res.data.data.total || 0
          return Promise.resolve(res); // 
        } else {
          // 40101request.js
          if (res.data.code !== 40101) {
            this.$message.error(res.data.message || "");
          }
          return Promise.reject(res); // 
        }
      }).catch((error) => {
        // 40101request.js
        if (!error.response || !error.response.data || error.response.data.code !== 40101) {
          this.$message.error("");
        }
        return Promise.reject(error); // 
      });
    },
    handleCurrentChange(val) {
      this.current = val
      this.getUserList()
    },
    showAddDialog() {
      this.addShow = true;
      this.addUserData = {
        name: '',
        username: '',
        password: '',
        department: '',
        phone: '',
        status: 1,
        roleIds: []
      }
      // 
      this.$nextTick(() => {
        setTimeout(() => {
          const firstInput = this.$el.querySelector('.el-dialog__body .el-input__inner');
          if (firstInput) {
            firstInput.focus();
          } else {
            const dialogBody = this.$el.querySelector('.el-dialog__body');
            if (dialogBody) {
              dialogBody.setAttribute('tabindex', '-1');
              dialogBody.focus();
            }
          }
        }, 200);
      });
    },
    showUpdateDialog(row) {
      this.updateShow = true;
      this.updateUserData = {
        id: row.id,
        name: row.name,
        username: row.username,
        department: row.department,
        phone: row.phone,
        status: row.status,
        roleIds: row.roles ? row.roles.map(r => r.id) : []
      }
      // 
      this.$nextTick(() => {
        setTimeout(() => {
          const firstInput = this.$el.querySelector('.el-dialog__body .el-input__inner');
          if (firstInput) {
            firstInput.focus();
          } else {
            const dialogBody = this.$el.querySelector('.el-dialog__body');
            if (dialogBody) {
              dialogBody.setAttribute('tabindex', '-1');
              dialogBody.focus();
            }
          }
        }, 200);
      });
    },
    showAssignRoleDialog(row) {
      this.assignRoleShow = true;
      this.assignRoleData = {
        id: row.id,
        name: row.name,
        roleIds: row.roles ? row.roles.map(r => r.id) : []
      }
      // 
      this.$nextTick(() => {
        setTimeout(() => {
          const firstInput = this.$el.querySelector('.el-dialog__body .el-input__inner, .el-dialog__body .el-select');
          if (firstInput) {
            if (firstInput.classList && firstInput.classList.contains('el-select')) {
              // 
              const selectInput = firstInput.querySelector('.el-input__inner');
              if (selectInput) {
                selectInput.focus();
              }
            } else {
              firstInput.focus();
            }
          } else {
            const dialogBody = this.$el.querySelector('.el-dialog__body');
            if (dialogBody) {
              dialogBody.setAttribute('tabindex', '-1');
              dialogBody.focus();
            }
          }
        }, 200);
      });
    },
    insertUserData() {
      this.$refs.addUserForm.validate((valid) => {
        if (valid) {
          addUser(this.addUserData).then(res => {
            if (res.data.code == 200) {
              this.$message.success("")
              this.getUserList()
              this.addShow = false
            } else {
              this.$message.error(res.data.message || "")
            }
          }).catch(() => {
            this.$message.error("")
          });
        }
      });
    },
    updateUserDataById() {
      this.$refs.updateUserForm.validate((valid) => {
        if (valid) {
          const updateData = {
            id: this.updateUserData.id,
            name: this.updateUserData.name,
            username: this.updateUserData.username,
            department: this.updateUserData.department,
            phone: this.updateUserData.phone,
            status: this.updateUserData.status,
            roles: this.updateUserData.roleIds.map(roleId => {
              const role = this.roleList.find(r => r.id === roleId);
              return role ? { id: roleId, roleName: role.roleName } : { id: roleId };
            })
          };
          updateUser(updateData).then(res => {
            if (res.data.code == 200) {
              this.$message.success("")
              this.getUserList()
              this.updateShow = false
            } else {
              this.$message.error(res.data.message || "")
            }
          }).catch(() => {
            this.$message.error("")
          });
        }
      });
    },
    assignRoleDataById() {
      if (!this.assignRoleData.roleIds || this.assignRoleData.roleIds.length === 0) {
        this.$message.warning("");
        return;
      }
      const assignData = {
        id: this.assignRoleData.id,
        roles: this.assignRoleData.roleIds.map(roleId => {
          const role = this.roleList.find(r => r.id === roleId);
          return role ? { id: roleId, roleName: role.roleName } : { id: roleId };
        })
      };
      assignRoles(assignData).then(res => {
        if (res.data.code == 200) {
          this.$message.success("")
          this.getUserList()
          this.assignRoleShow = false
        } else {
          this.$message.error(res.data.message || "")
        }
      }).catch(() => {
        this.$message.error("")
      });
    },
    resetUserPassword(row) {
      this.$confirm('"123456"', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        resetPassword({ id: row.id }).then(res => {
          if (res.data.code == 200) {
            this.$message.success("123456")
          } else {
            this.$message.error(res.data.message || "")
          }
        }).catch(() => {
          this.$message.error("")
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
    deleteUserById(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deleteUser({ id: row.id }).then(res => {
          if (res.data.code == 200) {
            this.$message.success("")
            this.getUserList()
          } else {
            this.$message.error(res.data.message || "")
          }
        }).catch(() => {
          this.$message.error("")
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
  }
}
</script>

<style scoped>
.cell {
  text-align: center;
}

.energyleft,
.energyright {
  display: inline-block;
}

.energyright {
  vertical-align: top !important;
  margin-bottom: 19px;
}

.el-table__body tr:hover>td {
  background-color: #e8f5e3 !important;
}

.form-item-inline {
  font-size: 13px;
}

.el-table__fixed {
  height: 100% !important;
}

/* .main-box-admin  common.css  */

.flex-box-header-new {
  margin-left: 20px;
  margin-right: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: start;
}
</style>




