import Vue from "vue";
import Router from "vue-router";
import HelloWorld from "../components/HelloWorld";
import Login from "../page/Login";

import TanHeader from "../components/TanHeader.vue";
import TanPage from "../page/tan/TanPage";
import TanContrast from "../page/tan/TanContrast";
import TanAudit from "../page/tan/TanAudit";
import TanMonitor from "../page/tan/TanMonitor";
import ExchangeSetting from "../page/admin/ExchangeSetting";
import ManageCarbon from "../page/admin/ManageCarbon";
import ManagePlace from "../page/admin/ManagePlace";
import ManageSchool from "../page/admin/ManageSchool";
import ManageUser from "../page/admin/ManageUser";
import ManageRole from "../page/admin/ManageRole";
import ManagePermission from "../page/admin/ManagePermission";
import TanResult from "../page/tan/TanResult";
import TanAnalyse from "../page/tan/TanAnalyse";
import TanExport from "../page/tan/TanExport";
import AdminHeader from "../components/AdminHeader";
import adminPage from "../page/admin/adminPage";
import { Message } from "element-ui";
Vue.prototype.$message = Message;
Vue.use(Router);

const router = new Router({
  routes: [
    {
      path: "/",
      redirect: "/Tan/TanPage"
    },
    {
      path: "/Login",
      redirect: "/Tan/TanLogin"
    },
    {
      path: "/Tan",
      name: "Tan",
      component: TanHeader,
      redirect: "/Tan/TanPage",
      children: [
        {
          path: "TanPage",
          name: "TanPage",
          component: TanPage
        },
        {
          path: "TanLogin",
          name: "TanLogin",
          component: Login
        },
        {
          path: "ManageSchool",
          name: "ManageSchool",
          component: ManageSchool,
          meta: {
            authRequired: true
            // @PreAuthorize
          }
        },
        {
          path: "ManageCarbon",
          name: "ManageCarbon",
          component: ManageCarbon,
          meta: {
            authRequired: true
            // @PreAuthorize
          }
        },
        {
          path: "ExchangeSetting",
          name: "ExchangeSetting",
          component: ExchangeSetting,
          meta: {
            authRequired: true
            // @PreAuthorize
          }
        },
        {
          path: "ManagePlace",
          name: "ManagePlace",
          component: ManagePlace,
          meta: {
            authRequired: true
            // @PreAuthorize
          }
        },
        {
          path: "TanContrast",
          name: "TanContrast",
          component: TanContrast,
          meta: {
            authRequired: true
          }
        },
        {
          path: "TanAudit",
          name: "TanAudit",
          component: TanAudit,
          meta: {
            authRequired: true
          }
        },
        {
          path: "TanMonitor",
          name: "TanMonitor",
          component: TanMonitor,
          meta: {
            authRequired: true
          }
        },
        {
          path: "TanResult",
          name: "TanResult",
          component: TanResult,
          meta: {
            authRequired: true
          }
        },
        {
          path: "TanAnalyse",
          name: "TanAnalyse",
          component: TanAnalyse,
          meta: {
            authRequired: true
          }
        },
        {
          path: "TanExport",
          name: "TanExport",
          component: TanExport,
          meta: {
            authRequired: true
          }
        },
        {
          path: "ManageUser",
          name: "ManageUser",
          component: ManageUser,
          meta: {
            authRequired: true,
            requireRole: "admin"
          }
        },
        {
          path: "ManageRole",
          name: "ManageRole",
          component: ManageRole,
          meta: {
            authRequired: true,
            requireRole: "admin"
          }
        },
        {
          path: "ManagePermission",
          name: "ManagePermission",
          component: ManagePermission,
          meta: {
            authRequired: true,
            requireRole: "admin"
          }
        }
      ]
    }
  ]
});

const originalPush = Router.prototype.push;
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err);
};
// 
router.beforeEach(async (to, from, next) => {
  const { authRequired, requireRole } = to.meta;
  
  // 
  if (to.name === "TanLogin") {
    next();
    return;
  }
  
  // 
  if (authRequired === true) {
    const token = localStorage.getItem("token");
    const auth_role = localStorage.getItem("auth");
    
    // token
    if (!token) {
      // 
      Vue.prototype.$message.warning("");
      next({
        name: "TanLogin",
        params: {
          curRoute: to.path
        }
      });
      return;
    }
    
    // 
    if (requireRole) {
      if (auth_role === requireRole) {
        next();
      } else {
        Vue.prototype.$message.warning("");
        next({
          name: "TanLogin",
          params: {
            curRoute: to.path
          }
        });
      }
    } else {
      // token
      next();
    }
    return;
  }
  
  //
  if (to.matched.length === 0) {
    next({
      path: "/Tan/TanPage"
    });
    return;
  }
  
  // 
  next();
});

router.afterEach((to, from) => {
  // 
  window.scrollTo(0, 0);
});

export default router;



