<template>
  <div class="main-box-admin">
    <el-breadcrumb separator="/" style="font-size: 18px; margin-bottom: 20px;">
      <el-breadcrumb-item :to="{ path: '/Tan/ManageCarbon' }">
        <i class="el-icon-s-home"></i> 
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="forest-card" style="padding: 20px; margin-bottom: 20px;">
      <div class="flex-box-header">
        <div class="energyleft">
          <el-form>
            <el-tag style="font-size: 13px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="name" placeholder="" size="small" style="width:200px"></el-input>
              <el-date-picker v-model="year" type="year" value-format='yyyy' placeholder="" size="small">
              </el-date-picker>
              <!-- <el-input v-model="month" placeholder="" size="small" style="width:200px"></el-input> -->
              <el-select v-model="month" placeholder="">
                <el-option v-for="item in 12" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </el-form-item>
            <el-button class="form-item-inline" size="small" @click="getEmission" plain
              icon="el-icon-search"></el-button>
            <el-button class="form-item-inline" type="success" size="small" @click="getAllEmissionClean" plain
              icon="el-icon-search"></el-button>
          </el-form>
        </div>
        <div class="energyright">
          <el-form>
            <el-button class="form-item-inline" type="primary" size="small" @click="insertEmission"></el-button>
            <el-button class="form-item-inline" type="primary" size="small" @click="getexcelEmission"
              style="margin: 0;"></el-button>
          </el-form>
        </div>
      </div>
      <div class="table_container" style="width: 100%; overflow-x: auto; overflow-y: visible;">
        <el-table :data="tableData" style="width: 100%;font-size: 15px; " size="small" class="forest-table">
          <el-table-column label="" min-width="100" align="center">
            <template slot-scope="scope">
              {{ (scope.$index + 1) + (current - 1) * limit }}
            </template>
          </el-table-column>
          <el-table-column prop="name" label="" min-width="150" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column prop="category" label="" min-width="150" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column prop="consumption" label="()" min-width="140" align="center"></el-table-column>
          <el-table-column prop="purpose" label="" min-width="120" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column prop="emissionType" label="" min-width="120" align="center">
            <template slot-scope="scope">
              {{ scope.row.emissionType === '0' ? '' : scope.row.emissionType === '1' ? '' : '' }}
            </template>
          </el-table-column>
          <!--          <el-table-column prop="emissionPurpose" label=""></el-table-column>-->
          <!--          <el-table-column prop="amount" label=""></el-table-column>-->
          <el-table-column prop="place" label="" min-width="150" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column prop="year" label="" min-width="140" align="center"></el-table-column>
          <el-table-column prop="month" label="" min-width="140" align="center"></el-table-column>
          <el-table-column label="" min-width="200" fixed="right" align="center">
            <template slot-scope="scope">
              <div class="updBtn" style="display: inline-block;"><el-button @click="updateEmission(scope.row)"
                  type="warning" plain size="small" icon="el-icon-edit"></el-button></div>
              <div class="delBtn" style="display: inline-block;"><el-button @click="deleteEmission(scope.row)"
                  type="danger" plain size="small" icon="el-icon-delete"></el-button></div>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div style="text-align:center; margin:20px;padding-right: 50px;">
          <el-pagination background @current-change="handleCurrentChange" :page-size="limit" :current-page.sync="current"
            layout="total, prev, pager, next" :total="total" v-if="pageshow">
          </el-pagination>
        </div>
      </div>
      <div class="upload-image">
        <el-dialog title="" :visible.sync="emissionInsertForm" :modal="false" width="40%">
          <el-form :model="newEmissionData" ref="ruleForm" label-width="180px">
            <el-form-item label="" prop="name">
              <el-input v-model="newEmissionData.name" style="width: 300px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="category">
              <el-select v-model="newEmissionData.category" placeholder="">
                <el-option v-for="item in categoryList" :key="item.id" :label="item.objectCategory"
                  :value="item.objectCategory">
                </el-option>
              </el-select>

            </el-form-item>
            <el-form-item label="()" prop="consumption">
              <el-input v-model="newEmissionData.consumption" @keyup.native="inputJudgeConsumption(1)" style="width: 300px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="purpose">
              <el-input v-model="newEmissionData.purpose" style="width: 300px"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="newEmissionData.emissionType" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="place">
              <el-select v-model="newEmissionData.place" placeholder="">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="year">
              <el-date-picker v-model="newEmissionData.year" type="year" value-format='yyyy' placeholder="">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="month">
              <el-select v-model="newEmissionData.month" placeholder="">
                <el-option v-for="item in 12" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
              <!-- <el-input v-model="newEmissionData.month" style="width: 300px"></el-input> -->
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="insertEmissionData"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
      <div class="upload-image">
        <el-dialog title="" :visible.sync="emissionUpdateForm" :modal="false" width="40%">
          <el-form :model="updateEmissionData" ref="ruleForm" label-width="180px">
            <el-form-item label="" prop="name">
              <el-input v-model="updateEmissionData.name" style="width: 300px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="category">
              <el-input v-model="updateEmissionData.category" style="width: 300px"></el-input>
            </el-form-item>
            <el-form-item label="()" prop="consumption">
              <el-input v-model="updateEmissionData.consumption" @keyup.native="inputJudgeConsumption(2)" style="width: 300px"></el-input>
            </el-form-item>
            <el-form-item label="" prop="purpose">
              <el-input v-model="updateEmissionData.purpose" style="width: 300px"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select v-model="updateEmissionData.emissionType" placeholder="">
                <el-option label="" value="0"></el-option>
                <el-option label="" value="1"></el-option>
                <el-option label="" value="2"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="place">
              <el-select v-model="updateEmissionData.place" placeholder="">
                <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" prop="year">
              <el-date-picker v-model="updateEmissionData.year" type="year" value-format='yyyy' placeholder="">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="month">
              <!-- <el-input v-model="updateEmissionData.month" style="width: 300px"></el-input> -->
              <el-select v-model="updateEmissionData.month" placeholder="">
                <el-option v-for="item in 12" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="modifyEmissionData"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
      <div class="upload-image" style="width: 300px">
        <el-dialog class="self_define" title="" :close-on-click-modal='false' :visible.sync="emissionAddForm" :modal="false"
          width="40%">
          <el-form :model="fileRes" ref="ruleForm" label-width="100px">
            <el-form-item label="" ref="uploadElement" prop="photoUrl" style="margin: 0 auto">
              <el-upload class="upload-demo" ref="upload" drag :action="UploadEnergyUrl()" multiple :auto-upload="false"
                       :on-success="handleSuccessEnergy" :on-error="uploadEnergyError" :before-upload="getFileType" :limit="5"
                       :file-list="fileList" name="file" :headers="uploadHeaders">
                <i class="el-icon-upload"></i>
                <div slot="tip" class="el-upload__tip" style="width: 100%; word-break:break-all;">
                  .xlsx xlsx<p style="font-weight: bold;">()</p>
                </div>
                <div class="el-upload__text"><em></em></div>
              </el-upload>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="addNewEmission"></el-button>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>
<script>
import request from "../../utils/request";
import { getAllEmission, searchCarbonByName } from "../../api/carbonEmission";
import { getPlace } from "../../api/placeInfo";
import { getAllCategory } from "../../api/exchangeSetting";
export default {
  data() {
    return {
      categoryList: [],
      options: [],
      current: 1,
      size: 10,
      cur: 1,
      fileRes: {},
      year: '',
      newEmissionData: {},
      updateEmissionData: {},
      fileList: [],
      tableData: [],
      emissionInsertForm: false,
      name: '',
      month: '',
      emissionAddForm: false,
      emissionUpdateForm: false,
             limit: 10,
             total: 0,
             pageshow: true,
           }
         },
         computed: {
           //  JWT Token
           uploadHeaders() {
             const token = localStorage.getItem('token');
             return token ? { 'Authorization': 'Bearer ' + token } : {};
           }
         },
  mounted: function () {
    this.getBuildingName()
    this.getAllEmission()
    this.currentPage = 1
  },
  activated() {
    this.getAllEmission()
  },

  methods: {
    inputJudgeConsumption(val) {
      if (val == 1 && (this.newEmissionData != null && this.newEmissionData.consumption != null)) {
        this.newEmissionData.consumption = this.newEmissionData.consumption.replace(/[^\.\d]/g, "");
      }
      else if (this.updateEmissionData != null && this.updateEmissionData.consumption != null) {
        this.updateEmissionData.consumption = this.updateEmissionData.consumption.replace(/[^\.\d]/g, "");
      }
    },
    getCategeoryList() {
      getAllCategory().then(res => {
        // console.log(res.data);
        this.categoryList = res.data
      }).catch(() => {
        this.$message.error("")
      }).finally(() => {
      });
    },
    //
    getBuildingName() {
      this.options = []; // 
      getPlace().then(res => {
        if (res.data.code == 200) {
          this.carbonInformation = res.data.data
          for (var i = 0; i < this.carbonInformation.length; i++) {
            //lablevalueel-selectv-modelvalue
            this.options.push({ label: this.carbonInformation[i].name, value: this.carbonInformation[i].name });
          }
        }
      })
    },
    getEmission() {
      this.current = 1;//cur_page 
      this.getAllEmission();//
      this.pageshow = false;//
      this.$nextTick(() => {//
        this.pageshow = true;

      });
    },
    getAllEmissionClean() {
      this.name = '';
      this.year = '';
      this.month = '';
      this.current = 1;//cur_page 
      this.getAllEmission();//
      this.pageshow = false;//
      this.$nextTick(() => {//
        this.pageshow = true;

      });
      // console.log("rhis.current", this.current)
    },
    updateEmission(row) {
      //debugger
      // console.log("row", row);
      this.updateEmissionData = row;
      //if(this.updateEmissionData.emissionYear==2023) {
      //  this.updateEmissionData.emissionYear = "2023";
      //}
      this.updateEmissionData.year = this.updateEmissionData.year.toString()
      // console.log(this.updateEmissionData.year)
      this.emissionUpdateForm = true;
    },
    modifyEmissionData() {
      // console.log("updateEmissionData",this.updateEmissionData)
      if (this.updateEmissionData["name"] === "" || this.updateEmissionData["category"] === '' || this.updateEmissionData["consumption"] === '' || this.updateEmissionData["emissionType"] === '') {
        this.$message.error("");
        return;
      }

      if (this.updateEmissionData.emissionType == "") {
        this.updateEmissionData.emissionType = "0";
      }
      if (this.updateEmissionData.emissionType == "") {
        this.updateEmissionData.emissionType = "1";
      }
      if (this.updateEmissionData.emissionType == "") {
        this.updateEmissionData.emissionType = "2";
      }
      // console.log("this.updateEmissionData.emissionType", this.updateEmissionData.emissionType)
      request.post("/api/carbonEmission/updateCarbonEmission", this.updateEmissionData).then((res) => {
        //debugger
        // console.log("")
        // console.log("res.data", res.data)
        if (res.data.code == 200) {
          this.$message.success(res.data.data);
        } else {
          this.$message.error(res.data.message);
        }
      }).catch(() => {

      }).finally(() => {
        this.getAllEmission();
        this.emissionUpdateForm = false;
        this.updateEmissionData = {};

      });

    },
    insertEmissionData() {
      if (this.newEmissionData["name"] === "" || this.newEmissionData["category"] === '' || this.newEmissionData["consumption"] === '' || this.newEmissionData["emissionType"] === '') {
        this.$message.error("");
        return;
      } else {
        //this.$refs.upload.submit();
        this.handleEmission();
      }
    },

    handleEmission() {
      // console.log("this.newEmissionData", this.newEmissionData)
      request.post("/api/carbonEmission/addCarbonEmission", this.newEmissionData).then((res) => {
        //debugger
        // console.log("res.data", res.data)
        if (res.data.code == 200) {
          this.$message.success(res.data.data);
        } else {
          this.$message.error(res.data.message);
        }
      }).catch(() => {

      }).finally(() => {
        this.getAllEmission();
        this.emissionInsertForm = false;
        this.newEmissionData = {};
      });//.then
    },
    insertEmission() {
      this.emissionInsertForm = true;
      this.getCategeoryList();
      this.$nextTick(() => {
        setTimeout(() => {
          const firstInput = this.$el.querySelector('.el-dialog__body .el-input__inner');
          if (firstInput) {
            firstInput.focus();
          } else {
            const dialogBody = this.$el.querySelector('.el-dialog__body');
            if (dialogBody) {
              dialogBody.setAttribute('tabindex', '-1');
              dialogBody.focus();
            }
          }
        }, 200);
      });
    },
    deleteEmission(row) {
      // console.log(row.id)
      var carbonEmissionId = row.id
      // console.log("carbonEmissionId", carbonEmissionId)
      this.$confirm("", "", { type: "info" }).then(() => {
        request.get("/api/carbonEmission/deleteCarbonEmissionById", { params: { carbonEmissionId: carbonEmissionId } }).then((res) => {
          if (res.data.code === 200) {
            this.$message.success("")
            // pageSize=1
            let totalPage = Math.ceil((this.total - 1) / this.size)
            let currentPage = this.current > totalPage ? totalPage : this.current
            this.current = currentPage < 1 ? 1 : currentPage
          } else {
            this.$message.error(res.data.message)
          }
        }).catch(() => {
        }).finally(() => {
          this.tableData.length = this.tableData.length - 1;
          if (this.tableData.length === 0) {
            this.cur = this.cur - 1;
            if (this.cur === 0) this.cur = 1;
          }
          this.getAllEmission();
        });
      }).catch(() => {
        this.$message.info("");
      });
    },
    handleCurrentChange(val) {
      this.current = val
      this.getAllEmission()
    },
    getFileType(file) {
      // console.log("filetype")
      // console.log("")
      // console.log(file.type)
      if (file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') {
        return true;
      } else {
        this.$message({
          message: "xlsx!",
          type: "warning",
        });
        return false;
      }
    },
    handleSuccessEnergy(res) {
      //debugger
      // console.log("zhaopian")
      // console.log(localStorage.getItem("userId"))
      // console.log("zhaopianxinxi")
      // console.log("res", res)
      if (res.code === 200) {
        this.$refs.upload.clearFiles();
        this.$message.success(res.data)
        this.emissionAddForm = false;
        this.fileRes = {};
        this.getAllEmission();
      } else {
        this.$refs.upload.clearFiles();
        this.$message.error(res.message);
        this.emissionAddForm = false;
        this.fileRes = {};
      }
    },
    uploadEnergyError() {
      this.$message.error(".xlsx");
    },
    UploadEnergyUrl: function () {
      // el-upload  URL
      // Token  :headers  computed 
      const baseURL = process.env.NODE_ENV === 'production' 
        ? 'http://101.200.39.170:8080' 
        : 'http://localhost:8080';
      return baseURL + "/api/carbonEmission/excelCarbonEmission";
    },
    addNewEmission() {
      var files = this.$refs.upload.uploadFiles
      if (files.length == 0) {
        this.$message.error("");
        return;
      }
      this.$refs.upload.submit();
    },
    getAllEmission() {
      // console.log(" this.current", this.current)
      // debugger
      getAllEmission({ current: this.current, size: this.size, name: this.name, year: this.year, month: this.month }).then(res => {
        // debugger
        // console.log("res")
        // console.log(res)
        this.tableData = res.data.records
        this.total = res.data.total;
        for (var i = 0; i < this.total; i++) {
          if (this.tableData[i] != null && this.tableData[i].emissionType == "0") {
            this.tableData[i].emissionType = "";
          }
          if (this.tableData[i] != null && this.tableData[i].emissionType == "1") {
            this.tableData[i].emissionType = "";
          }
          if (this.tableData[i] != null && this.tableData[i].emissionType == "2") {
            this.tableData[i].emissionType = "";
          }
        }

        this.tableData.emissionType = ''
        // console.log(res.data.total);
      })
    },
    getexcelEmission() {
      //debugger
      this.emissionAddForm = true
    },
  }
}

</script>
<style scoped>
.el-input__inner {
  height: 32.5px;
}

.cell {
  text-align: center;
}

.energyleft,
.energyright {
  display: inline-block;
}

.energyright {
  vertical-align: top !important;
  margin-bottom: 19px;
}

.el-table__body tr:hover>td {
  background-color: #e8f5e3 !important;
}

.form-item-inline {
  font-size: 13px;
}

.el-table__fixed {
  height: 100% !important;
}

.main-box-admin {
  width: 94%;
  margin: 20px auto 0;
  text-align: left;
  padding: 5px; /* 3%5px */
}

.flex-box-header {
  margin-left: 20px;
  margin-right: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>



