    // 
    exportReport(format) {
      // 
      if (parseFloat(this.carbonInformation.totalEmission || 0).toFixed(2) == 0.0) {
        this.$notify({
          title: '',
          message: '',
          type: 'warning',
          offset: 100
        });
        return;
      }
      
      // 
      if (!this.exportStatus) {
        return;
      }
      
      this.exportStatus = false;
      if (this.msg) {
        this.msg.close();
      }
      
      // 
      const formatName = format === 'pdf' ? 'PDF' : 'Word';
      this.msg = this.$message({
        duration: 0,
        type: 'info',
        message: `${formatName}...`
      });
      
      // 
      const params = {
        format: format // 
      };
      if (this.showYear && this.showYear !== '') {
        params.year = this.showYear;
      }
      
      // 
      createExportTask(params).then((res) => {
        console.log(':', res);
        if (res.data && res.data.code === 200 && res.data.data && res.data.data.taskId) {
          const taskId = res.data.data.taskId;
          console.log('ID:', taskId);
          this.currentTaskId = taskId;
          
          // 
          this.msg.close();
          this.msg = this.$message({
            duration: 0,
            type: 'info',
            message: `${formatName}...`
          });
          
          // 
          this.startPollingTaskStatus(taskId, format, formatName);
        } else {
          this.exportStatus = true;
          this.msg.close();
          const errorMsg = res.data && res.data.message ? res.data.message : "";
          console.error(':', res);
          this.$message.error(errorMsg);
        }
      }).catch((error) => {
        this.exportStatus = true;
        this.msg.close();
        console.error(`:`, error);
        this.$message.error("" + (error.message || ""));
      });
    },
    
    // 
    startPollingTaskStatus(taskId, format, formatName) {
      // 
      if (this.pollingInterval) {
        clearInterval(this.pollingInterval);
        this.pollingInterval = null;
      }
      
      // 
      this.pollingErrorCount = 0;
      
      // 
      this.checkTaskStatus(taskId, format, formatName);
      
      // 2
      this.pollingInterval = setInterval(() => {
        this.checkTaskStatus(taskId, format, formatName);
      }, 2000);
    },
    
    // 
    checkTaskStatus(taskId, format, formatName) {
      getExportTaskStatus(taskId).then((res) => {
        console.log(':', res);
        
        if (res.data && res.data.code === 200 && res.data.data) {
          // 
          this.pollingErrorCount = 0;
          
          const taskData = res.data.data;
          const status = taskData.status;
          console.log(':', status);
          
          if (status === 'COMPLETED') {
            // 
            this.stopPolling();
            this.downloadTaskFile(taskId, taskData.fileName, format, formatName);
          } else if (status === 'FAILED') {
            // 
            this.stopPolling();
            this.exportStatus = true;
            if (this.msg) {
              this.msg.close();
            }
            this.$message.error(`${taskData.errorMessage || ''}`);
          } else if (status === 'PROCESSING') {
            // 
            if (!this.msg || this.msg.message !== `${formatName}...`) {
              if (this.msg) {
                this.msg.close();
              }
              this.msg = this.$message({
                duration: 0,
                type: 'info',
                message: `${formatName}...`
              });
            }
          }
          // PENDING
        } else if (res.data && res.data.code !== 200) {
          // 
          this.pollingErrorCount++;
          console.warn(` (${this.pollingErrorCount}/${this.maxPollingErrors}):`, res.data);
          
          // 
          if (this.pollingErrorCount >= this.maxPollingErrors) {
            const errorMsg = res.data.message || res.data.description || "";
            this.stopPolling();
            this.exportStatus = true;
            if (this.msg) {
              this.msg.close();
            }
            this.$message.error(errorMsg);
          }
        } else {
          // 
          this.pollingErrorCount++;
          console.warn(` (${this.pollingErrorCount}/${this.maxPollingErrors}):`, res);
          
          if (this.pollingErrorCount >= this.maxPollingErrors) {
            this.stopPolling();
            this.exportStatus = true;
            if (this.msg) {
              this.msg.close();
            }
            this.$message.error("");
          }
        }
      }).catch((error) => {
        console.error(':', error);
        // 
        this.pollingErrorCount++;
        if (this.pollingErrorCount >= this.maxPollingErrors) {
          this.stopPolling();
          this.exportStatus = true;
          if (this.msg) {
            this.msg.close();
          }
          this.$message.error("");
        }
      });
    },
    
    // 
    stopPolling() {
      if (this.pollingInterval) {
        clearInterval(this.pollingInterval);
        this.pollingInterval = null;
      }
      this.currentTaskId = null;
    },
    
    // 
    downloadTaskFile(taskId, fileName, format, formatName) {
      downloadExportTask(taskId).then((res) => {
        if (res.data != null) {
          this.msg.close();
          this.msg = this.$message({
            duration: 2000,
            type: 'success',
            message: `${formatName}`
          });
          this.exportStatus = true;
          
          // MIME
          const mimeType = format === 'pdf' ? "application/pdf;charset=UTF-8" : "application/vnd.openxmlformats-officedocument.wordprocessingml.document;charset=UTF-8";
          
          // 
          let link = document.createElement("a");
          let blob = new Blob([res.data], {
            type: mimeType,
          });
          link.style.display = "none";
          link.href = URL.createObjectURL(blob);
          link.download = fileName || `${this.showYear || new Date().getFullYear()}.${format}`;
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);
          
          // URL
          URL.revokeObjectURL(link.href);
        } else {
          this.exportStatus = true;
          this.msg.close();
          this.$message.error("");
        }
      }).catch((error) => {
        this.exportStatus = true;
        this.msg.close();
        console.error(`:`, error);
        this.$message.error("" + (error.message || ""));
      });
    },
    // PDF
    exportToPDF() {
      this.exportReport('pdf');
    },
    // Word
    exportToWord() {
      this.exportReport('docx');
    },
  },
  // 
  beforeDestroy() {
    this.stopPolling();
    if (this.msg) {
      this.msg.close();
    }
  },
}
</script>
<style scoped>
p {
  font-size: 22px;
  padding-left: 6.5%;
  text-align: left;
  text-indent: 2em;
}

.bottom {
  border-top: 0;
  /* box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04); */
}

.hr-shadow {
  margin-top: 50px;
  padding-top: 10px;
  border-left: 0;
  border-right: 0;
  border-bottom: 0;
  color: #d0d0d5;
  border-top: 10px solid rgba(0, 0, 0, .1);
  box-shadow: inset 0 10px 10px -10px;
}

.tanResult {
  width: 100%;
  background-color: var(--forest-bg-primary);
  text-align: left;
  margin: 0;
  margin-top: 10px;
  padding: 20px 20px 0 20px;
  box-sizing: border-box;
  min-height: calc(100vh - 100px);
}

.tanExport.tanResult {
  text-align: center;
}

.forest-card {
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(45, 80, 22, 0.1);
  overflow: hidden;
  background: #fff;
}

.forest-card:hover {
  box-shadow: 0 4px 16px rgba(45, 80, 22, 0.15);
}

.forest-card .forest-card-header {
  overflow: hidden;
  border-radius: 12px 12px 0 0;
}

.forest-card-header {
  background: linear-gradient(135deg, #4a7c3a 0%, #2d5016 100%);
  color: white;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 18px;
  font-weight: 600;
}

.forest-card-header i {
  font-size: 20px;
}

/* PDF */
.export-controls {
  background: #fff;
  width: 210mm;
  padding: 15px 20px;
  margin: 0 auto 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  box-sizing: border-box;
}

.pdf-preview-container {
  background: #fff;
  width: 210mm; /* A4 */
  min-height: 297mm; /* A4 */
  margin: 0 auto;
  padding: 20mm;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}

.double-page-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  background: #f5f5f5;
}

.a4-page {
  width: 210mm;
  min-height: 297mm;
  padding: 20mm;
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  box-sizing: border-box;
  page-break-after: always;
  overflow: hidden;
}

.exportTitle {
  font-family: "SimSun", "", serif;
  line-height: 1.6;
  color: #333;
}

.exportTitle p {
  text-indent: 2em;
  margin: 10px 0;
}

/*  */
@media print {
  .export-controls {
    display: none;
  }
  
  .pdf-preview-container {
    width: 100%;
    min-height: auto;
    margin: 0;
    padding: 0;
    box-shadow: none;
  }
  
  .tanExport {
    background: #fff;
  }
}

.flex-box-headerr {
  margin-left: 20px;
  margin-right: 15px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.junzhi,
.zhuzhuangtu {
  display: inline-block;

}

.junzhi {
  width: 370px;
  margin-right: 10px;

}

.zhuzhuangtu {
  display: inline-block;
  margin-top: 20px;
  width: 100%;
  max-width: 650px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  margin-bottom: 10px;
}

.duijizhu {
  display: inline-block;
  width: 100%;
  max-width: 650px;
  height: 700px;
  margin: 0 auto;
}

.bingtu {
  display: inline-block;
  width: 100%;
  max-width: 600px;
  height: 480px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
  margin: 0 auto;
}
</style>
<style>
.el-input__inner {
  height: 32.5px;
}
</style>


