import request from "../utils/request";

// ============================================
// API
// ============================================

//
export function getSchoolInfo() {
  return request({
    url: "/api/school/getSchoolInfo",
    method: "get"
  });
}

//
export function updateSchoolInfo(params) {
  return request({
    url: "/api/school/updateSchoolInfo",
    method: "post",
    data: params
  });
}



