import request from "../utils/request";

// ============================================
// API
// ============================================

//
export function login(params) {
  return request({
    url: "/api/user/login",
    method: "post",
    data: params
  });
}

//Token
export function refreshTokenApi(params) {
  return request({
    url: "/api/user/refreshToken",
    method: "post",
    data: params
  });
}

//
export function logoutApi() {
  return request({
    url: "/api/user/logout",
    method: "post"
  });
}




