<script>
import pubsub from 'pubsub-js'
import { logoutApi } from '../api/user'
import tokenManager from '../utils/tokenManager'

export default {
  name: 'TanHeader',
  data() {
    return {
      loginForm: false,
      isLogin: 'youke',
      logoUrl: require('../assets/bjfu-logo.png')
    }
  },
  props: {
  },
  created() {
    this.$bus.$on('updateLoginStatus', (data) => {
      this.isLogin = data
    })
    this.checkLoginStatus()
  },
  watch: {
    '$route'() {
      this.checkLoginStatus()
    }
  },
  methods: {
    checkLoginStatus() {
      const token = tokenManager.getAccessToken()
      const auth = localStorage.getItem('auth')
      if (token && auth) {
        this.isLogin = auth
      } else {
        this.isLogin = 'youke'
      }
    },
    async handleCommand(command) {
      if (command == 'signout') {
        try {
          await logoutApi()
        } catch (error) {
          console.error('Logout API error:', error)
        }
        tokenManager.clearTokens()
        localStorage.removeItem("userId")
        localStorage.removeItem("auth")
        this.$message.success('')
        this.isLogin = 'youke'
        this.$bus.$emit('updateLoginStatus', 'youke')
        this.$router.push({ path: '/Tan/TanPage' })
      } else if (command == 'login') {
        this.$router.push({ path: '/Tan/TanLogin' })
      } else if (command == 'backtoindex') {
        this.$router.push({ path: '/Tan/adminPage' })
      }
    },
    jumpCarbon() {
      // console.log("this.$route.path:",this.$route.path)
      this.$router.push({
        path: 'ManageCarbon',
      });
    },
    handleImageError(e) {
      console.error('Logo:', e);
      // logologo
      if (e.target && e.target.parentElement) {
        e.target.parentElement.style.display = 'none';
      }
    },
    jump(value) {
      //1.localStorage
      // localStorage.setItem("loginForm",true);

      this.$router.push({
        //2.
        // path:'Login',
        // query:{
        //   loginForm: true
        // }
        //params path
        name: 'TanLogin',
        // params:{
        //   loginForm: value
        // }
      })

      //3.
      // this.loginForm = value
      // this.$bus.$emit('sendLoginForm',this.loginForm)

      //4.
      // this.loginForm = value
      // pubsub.publish('sendLoginForm', value)
    }
  },

  beforeDestroy() {
    //3.
    // this.jump(this.loginForm)

    //4.
    // pubsub.publish('sendLoginForm', this.loginForm)
  }
}
</script>

