<template>
  <div class="main-box-admin">
    <el-breadcrumb separator="/" style="font-size: 18px; margin-bottom: 20px;">
      <el-breadcrumb-item :to="{ path: '/Tan/TanPage' }">
        <i class="el-icon-s-home"></i> 
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="forest-card" style="padding: 20px; margin-bottom: 20px;">
      <div class="flex-box-header-new">
        <div class="energyleft">
          <el-form>
            <el-tag style="font-size: 13px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="searchPermissionCode" placeholder="" size="small" style="width:150px"></el-input>
            </el-form-item>
            <el-tag style="font-size: 13px; margin-left: 10px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="searchPermissionName" placeholder="" size="small" style="width:150px"></el-input>
            </el-form-item>
            <el-tag style="font-size: 13px; margin-left: 10px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-select v-model="searchPermissionType" placeholder="" size="small" style="width:120px" clearable>
                <el-option label="" value="menu"></el-option>
                <el-option label="" value="button"></el-option>
                <el-option label="" value="api"></el-option>
              </el-select>
            </el-form-item>
            <el-button class="form-item-inline" size="small" @click="filterPermissionTree" plain
              icon="el-icon-search"></el-button>
            <el-button class="form-item-inline" type="success" size="small" @click="getAllPermissionList" plain
              icon="el-icon-search"></el-button>
          </el-form>
        </div>
        <div style="margin-left: 50px;margin-bottom: 21px;">
          <el-form>
            <el-button class="form-item-inline" type="primary" size="small" @click="showAddDialog(null)" style="white-space: nowrap;"></el-button>
          </el-form>
        </div>
      </div>
      <div class="table_container" style="width: 100%; overflow-x: auto; overflow-y: visible;">
        <el-table
          :data="filteredPermissionTreeData"
          style="width: 100%;font-size: 15px;"
          size="small"
          class="forest-table"
          :tree-props="{children: 'children', hasChildren: 'hasChildren'}"
          row-key="id"
          :default-expand-all="true"
          :key="tableKey">
          <el-table-column label="" prop="permissionCode" min-width="340px" show-overflow-tooltip></el-table-column>
          <el-table-column label="" prop="permissionName" min-width="200px" show-overflow-tooltip></el-table-column>
          <el-table-column label="" prop="permissionType" width="120px">
            <template slot-scope="scope">
              <el-tag :type="scope.row.permissionType === 'menu' ? 'success' : scope.row.permissionType === 'button' ? 'warning' : 'info'" size="small">
                {{ scope.row.permissionType === 'menu' ? '' : scope.row.permissionType === 'button' ? '' : '' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="" prop="path" min-width="200px" show-overflow-tooltip></el-table-column>
          <el-table-column label="" prop="icon" width="120px">
            <template slot-scope="scope">
              <i :class="scope.row.icon" v-if="scope.row.icon"></i>
              <span v-else>-</span>
            </template>
          </el-table-column>
          <el-table-column label="" prop="sortOrder" width="80px"></el-table-column>
          <el-table-column label="" prop="status" width="100px">
            <template slot-scope="scope">
              <el-tag :type="scope.row.status === 1 ? 'success' : 'info'" size="small">
                {{ scope.row.status === 1 ? '' : '' }}
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="320px" fixed="right" style="white-space:nowrap;">
            <template slot-scope="scope">
              <el-button @click="showAddDialog(scope.row)" type="success" plain size="mini" icon="el-icon-plus" style="margin: 2px;"></el-button>
              <el-button @click="showUpdateDialog(scope.row)" type="warning" plain size="mini" icon="el-icon-edit" style="margin: 2px;"></el-button>
              <el-button @click="deletePermissionById(scope.row)" type="danger" plain size="mini" icon="el-icon-delete" style="margin: 2px;"></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <!--  -->
      <el-dialog :title="addPermissionData.parentId === 0 || !addPermissionData.parentId ? '' : ''" :visible.sync="addShow" :modal="false" top="10%" width="60%">
        <el-form :model="addPermissionData" ref="addPermissionForm" label-width="120px" :rules="permissionRules">
          <el-form-item label="" prop="permissionCode">
            <el-input v-model="addPermissionData.permissionCode" style="width: 400px" placeholder="USER_MANAGE"></el-input>
          </el-form-item>
          <el-form-item label="" prop="permissionName">
            <el-input v-model="addPermissionData.permissionName" style="width: 400px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="permissionType">
            <el-select v-model="addPermissionData.permissionType" placeholder="" style="width: 400px">
              <el-option label="" value="menu"></el-option>
              <el-option label="" value="button"></el-option>
              <el-option label="" value="api"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="parentId">
            <el-input v-model="addPermissionData.parentName" :disabled="true" style="width: 400px" v-if="addPermissionData.parentId && addPermissionData.parentId !== 0"></el-input>
            <span v-else style="color: #999;"></span>
          </el-form-item>
          <el-form-item label="" prop="path" v-if="addPermissionData.permissionType === 'menu'">
            <el-input v-model="addPermissionData.path" style="width: 400px" placeholder="/Tan/ManageUser"></el-input>
          </el-form-item>
          <el-form-item label="" prop="component" v-if="addPermissionData.permissionType === 'menu'">
            <el-input v-model="addPermissionData.component" style="width: 400px" placeholder="ManageUser"></el-input>
          </el-form-item>
          <el-form-item label="" prop="icon" v-if="addPermissionData.permissionType === 'menu'">
            <el-input v-model="addPermissionData.icon" style="width: 400px" placeholder="el-icon-user"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sortOrder">
            <el-input-number v-model="addPermissionData.sortOrder" :min="0" style="width: 400px"></el-input-number>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input v-model="addPermissionData.description" type="textarea" :rows="3" style="width: 400px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="addPermissionData.status" placeholder="" style="width: 400px">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="insertPermissionData"></el-button>
            <el-button @click="addShow = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="updateShow" :modal="false" top="10%" width="60%">
        <el-form :model="updatePermissionData" ref="updatePermissionForm" label-width="120px" :rules="permissionRules">
          <el-form-item label="" prop="permissionCode">
            <el-input v-model="updatePermissionData.permissionCode" style="width: 400px" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="" prop="permissionName">
            <el-input v-model="updatePermissionData.permissionName" style="width: 400px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="permissionType">
            <el-select v-model="updatePermissionData.permissionType" placeholder="" style="width: 400px">
              <el-option label="" value="menu"></el-option>
              <el-option label="" value="button"></el-option>
              <el-option label="" value="api"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="path" v-if="updatePermissionData.permissionType === 'menu'">
            <el-input v-model="updatePermissionData.path" style="width: 400px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="component" v-if="updatePermissionData.permissionType === 'menu'">
            <el-input v-model="updatePermissionData.component" style="width: 400px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="icon" v-if="updatePermissionData.permissionType === 'menu'">
            <el-input v-model="updatePermissionData.icon" style="width: 400px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sortOrder">
            <el-input-number v-model="updatePermissionData.sortOrder" :min="0" style="width: 400px"></el-input-number>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input v-model="updatePermissionData.description" type="textarea" :rows="3" style="width: 400px" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-select v-model="updatePermissionData.status" placeholder="" style="width: 400px">
              <el-option label="" :value="1"></el-option>
              <el-option label="" :value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="updatePermissionDataById"></el-button>
            <el-button @click="updateShow = false"></el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getPermissionTree, addPermission, updatePermission, deletePermission } from "../../api/permission";
export default {
  data() {
    return {
      permissionTreeData: [],
      filteredPermissionTreeData: [],
      addShow: false,
      updateShow: false,
      isTableExpanded: true, // 
      tableKey: 0, // 

      searchPermissionCode: '',
      searchPermissionName: '',
      searchPermissionType: '',

      addPermissionData: {
        permissionCode: '',
        permissionName: '',
        permissionType: 'menu',
        parentId: 0,
        parentName: '',
        path: '',
        component: '',
        icon: '',
        sortOrder: 0,
        description: '',
        status: 1
      },
      updatePermissionData: {},

      permissionRules: {
        permissionCode: [{ required: true, message: '', trigger: 'blur' }],
        permissionName: [{ required: true, message: '', trigger: 'blur' }],
        permissionType: [{ required: true, message: '', trigger: 'change' }]
      }
    }
  },
  mounted: function () {
    this.getPermissionTreeList()
  },
  activated() {
    this.getPermissionTreeList()
  },

  methods: {
    getPermissionTreeList() {
      getPermissionTree().then(res => {
        if (res.data.code == 200) {
          this.permissionTreeData = res.data.data || []
          this.filteredPermissionTreeData = res.data.data || []
          //  default-expand-all
          this.$nextTick(() => {
            this.isTableExpanded = true
            this.tableKey += 1 // 
          })
        } else {
          // 40101request.js
          if (res.data.code !== 40101) {
            this.$message.error(res.data.message || "");
          }
        }
      }).catch((error) => {
        // 40101request.js
        if (!error.response || !error.response.data || error.response.data.code !== 40101) {
          this.$message.error("");
        }
      });
    },
    getAllPermissionList() {
      this.searchPermissionCode = ''
      this.searchPermissionName = ''
      this.searchPermissionType = ''
      this.filteredPermissionTreeData = JSON.parse(JSON.stringify(this.permissionTreeData))
      // 
      this.$nextTick(() => {
        this.isTableExpanded = true
        this.tableKey += 1
      })
    },
    filterPermissionTree() {
      if (!this.searchPermissionCode && !this.searchPermissionName && !this.searchPermissionType) {
        this.filteredPermissionTreeData = JSON.parse(JSON.stringify(this.permissionTreeData))
        return
      }

      const filterNode = (node) => {
        // 
        const codeMatch = !this.searchPermissionCode || 
          (node.permissionCode && node.permissionCode.toLowerCase().includes(this.searchPermissionCode.toLowerCase()))
        const nameMatch = !this.searchPermissionName || 
          (node.permissionName && node.permissionName.includes(this.searchPermissionName))
        const typeMatch = !this.searchPermissionType || node.permissionType === this.searchPermissionType

        // 
        let filteredChildren = []
        if (node.children && node.children.length > 0) {
          filteredChildren = node.children.map(child => filterNode(child)).filter(child => child !== null)
        }

        // 
        if (codeMatch && nameMatch && typeMatch) {
          return {
            ...node,
            children: filteredChildren
          }
        } else if (filteredChildren.length > 0) {
          // 
          return {
            ...node,
            children: filteredChildren
          }
        }

        return null
      }

      this.filteredPermissionTreeData = this.permissionTreeData
        .map(node => filterNode(node))
        .filter(node => node !== null)
      // 
      this.$nextTick(() => {
        this.isTableExpanded = true
        this.tableKey += 1
      })
    },
    showAddDialog(parent) {
      this.addShow = true;
      if (parent) {
        this.addPermissionData = {
          permissionCode: '',
          permissionName: '',
          permissionType: 'menu',
          parentId: parent.id,
          parentName: parent.permissionName,
          path: '',
          component: '',
          icon: '',
          sortOrder: 0,
          description: '',
          status: 1
        }
      } else {
        this.addPermissionData = {
          permissionCode: '',
          permissionName: '',
          permissionType: 'menu',
          parentId: 0,
          parentName: '',
          path: '',
          component: '',
          icon: '',
          sortOrder: 0,
          description: '',
          status: 1
        }
      }
      this.$nextTick(() => {
        setTimeout(() => {
          const firstInput = this.$el.querySelector('.el-dialog__body .el-input__inner');
          if (firstInput) {
            firstInput.focus();
          } else {
            const dialogBody = this.$el.querySelector('.el-dialog__body');
            if (dialogBody) {
              dialogBody.setAttribute('tabindex', '-1');
              dialogBody.focus();
            }
          }
        }, 200);
      });
    },
    showUpdateDialog(row) {
      this.updateShow = true;
      this.updatePermissionData = {
        id: row.id,
        permissionCode: row.permissionCode,
        permissionName: row.permissionName,
        permissionType: row.permissionType,
        parentId: row.parentId,
        path: row.path || '',
        component: row.component || '',
        icon: row.icon || '',
        sortOrder: row.sortOrder || 0,
        description: row.description || '',
        status: row.status
      }
    },
    insertPermissionData() {
      this.$refs.addPermissionForm.validate((valid) => {
        if (valid) {
          addPermission(this.addPermissionData).then(res => {
            if (res.data.code == 200) {
              this.$message.success("")
              this.getPermissionTreeList()
              this.addShow = false
            } else {
              this.$message.error(res.data.message || "")
            }
          }).catch(() => {
            this.$message.error("")
          });
        }
      });
    },
    updatePermissionDataById() {
      this.$refs.updatePermissionForm.validate((valid) => {
        if (valid) {
          updatePermission(this.updatePermissionData).then(res => {
            if (res.data.code == 200) {
              this.$message.success("")
              this.getPermissionTreeList()
              this.updateShow = false
            } else {
              this.$message.error(res.data.message || "")
            }
          }).catch(() => {
            this.$message.error("")
          });
        }
      });
    },
    deletePermissionById(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deletePermission({ id: row.id }).then(res => {
          if (res.data.code == 200) {
            this.$message.success("")
            this.getPermissionTreeList()
          } else {
            this.$message.error(res.data.message || "")
          }
        }).catch(() => {
          this.$message.error("")
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
  }
}
</script>

<style scoped>
.energyleft,
.energyright {
  display: inline-block;
}

.energyright {
  vertical-align: top !important;
  margin-bottom: 19px;
}

.el-table__body tr:hover>td {
  background-color: #e8f5e3 !important;
}

.el-table .el-table__expand-icon {
  color: #666;
}

.el-table .el-table__expand-icon:hover {
  color: #4CAF50;
}

.form-item-inline {
  font-size: 13px;
}

.el-table__fixed {
  height: 100% !important;
}

.main-box-admin {
  width: 94%;
  margin: 20px auto 0;
  text-align: left;
  padding: 5px; /* 3%5px */
}

.flex-box-header-new {
  margin-left: 20px;
  margin-right: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: start;
}
</style>




