<template>
  <div class="main-box-admin">
    <el-breadcrumb separator="/" style="font-size: 18px; margin-bottom: 20px;">
      <el-breadcrumb-item :to="{ path: '/Tan/ManageCarbon' }">
        <i class="el-icon-s-home"></i> 
      </el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="forest-card" style="padding: 20px; margin-bottom: 20px;">
      <div class="flex-box-header-new">
        <div class="energyleft">
          <el-form>
            <el-tag style="font-size: 13px;"></el-tag>
            <el-form-item style="display: inline-block">
              <el-input v-model="searchPlaceName" placeholder="" size="small" style="width:200px"></el-input>
            </el-form-item>
            <el-button class="form-item-inline" size="small" @click="getPlaceList" plain
              icon="el-icon-search"></el-button>
            <el-button class="form-item-inline" type="success" size="small" @click="getAllPlaceList" plain
              icon="el-icon-search"></el-button>
          </el-form>
        </div>
        <div style="margin-left: 50px;margin-bottom: 21px;">
          <el-form>
            <el-button class="form-item-inline" type="primary" size="small" @click="showAddDialog"></el-button>
          </el-form>
        </div>
      </div>
      <div class="table_container" style="width: 100%; overflow-x: auto; overflow-y: visible;">
        <el-table :data="tableData" style="width: 100%;font-size: 15px;" size="small" class="forest-table">
          <el-table-column label="" min-width="100" align="center">
            <template slot-scope="scope">
              {{ (scope.$index + 1) + (current - 1) * limit }}
            </template>
          </el-table-column>
          <el-table-column prop="name" label="" min-width="200" align="center" show-overflow-tooltip></el-table-column>
          <el-table-column prop="population" label="" min-width="150" align="center"></el-table-column>
          <el-table-column prop="area" label="(m)" min-width="180" align="center"></el-table-column>
          <el-table-column label="" min-width="200" fixed="right" align="center">
            <template slot-scope="scope">
              <div class="updBtn" style="display: inline-block;">
                <el-button @click="showUpdateDialog(scope.row)" type="warning" plain size="small" icon="el-icon-edit"></el-button>
              </div>
              <div class="delBtn" style="display: inline-block;">
                <el-button @click="deletePlaceInfoById(scope.row)" type="danger" plain size="small"
                  icon="el-icon-delete"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div style="width: 100%; text-align:center; margin:20px;padding-right: 50px;">
          <el-pagination background @current-change="handleCurrentChange" :page-size="limit" :current-page.sync="current"
            layout="total, prev, pager, next" :total="total" v-if="pageshow">
          </el-pagination>
        </div>
      </div>
      <div>
        <el-dialog title="" :visible.sync="addShow" :modal="false" top="15%" width="40%">
          <el-form :model="addPlaceData" ref="ruleForm" label-width="180px">
            <el-form-item label="" prop="name">
              <el-input v-model="addPlaceData.name" style="width: 300px" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="population">
              <el-input v-model="addPlaceData.population" style="width: 300px" placeholder="" @keyup.native="inputJudgePopulation(1)"></el-input>
            </el-form-item>
            <el-form-item label="(m)" prop="area">
              <el-input v-model="addPlaceData.area" style="width: 300px" placeholder="" @keyup.native="inputJudgeArea(1)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="insertPlaceData"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
      <div class="upload-image">
        <el-dialog title="" :visible.sync="updateShow" :modal="false" top="15%" width="40%">
          <el-form :model="updatePlaceData" label-width="180px">
            <el-form-item label="" prop="name">
              <el-input v-model="updatePlaceData.name" style="width: 300px" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="population">
              <el-input v-model="updatePlaceData.population" style="width: 300px" placeholder="" @keyup.native="inputJudgePopulation(2)"></el-input>
            </el-form-item>
            <el-form-item label="(m)" prop="area">
              <el-input v-model="updatePlaceData.area" style="width: 300px" placeholder="" @keyup.native="inputJudgeArea(2)"></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="updatePlaceDataById"></el-button>
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
    </div>
  </div>
</template>
<script>
import { getAllPlaceInfo, addPlaceInfo, updatePlaceInfo, deletePlaceInfo } from "../../api/placeInfo";
export default {
  data() {
    return {
      tableData: [],
      current: 1,
      size: 10,
      limit: 10,
      total: 0,
      pageshow: true,
      addShow: false,
      updateShow: false,

      addPlaceData: {},
      updatePlaceData: {},

      searchPlaceName: '',
    }
  },
  mounted: function () {
    this.currentPage = 1
    this.getPlaceList()
  },
  activated() {
    this.getPlaceList()
  },

  methods: {
    inputJudgePopulation(val) {
      if (val == 1 && (this.addPlaceData != null && this.addPlaceData.population != null)) {
        this.addPlaceData.population = this.addPlaceData.population.toString().replace(/[^\d]/g, "");
      }
      else if (this.updatePlaceData != null && this.updatePlaceData.population != null) {
        this.updatePlaceData.population = this.updatePlaceData.population.toString().replace(/[^\d]/g, "");
      }
    },
    inputJudgeArea(val) {
      if (val == 1 && (this.addPlaceData != null && this.addPlaceData.area != null)) {
        this.addPlaceData.area = this.addPlaceData.area.toString().replace(/[^\.\d]/g, "");
      }
      else if (this.updatePlaceData != null && this.updatePlaceData.area != null) {
        this.updatePlaceData.area = this.updatePlaceData.area.toString().replace(/[^\.\d]/g, "");
      }
    },
    getAllPlaceList() {
      this.searchPlaceName = ""
      this.getPlaceList();
    },
    getPlaceList() {
      getAllPlaceInfo({ current: this.current, size: this.size, name: this.searchPlaceName }).then(res => {
        this.tableData = res.data.records || res.data;
        this.total = res.data.total || (res.data.length || 0);
      }).catch(() => {
        this.$message.error("");
      });
    },
    handleCurrentChange(val) {
      this.current = val
      this.getPlaceList()
    },
    showAddDialog() {
      this.addShow = true;
      this.addPlaceData = {}
      this.$nextTick(() => {
        setTimeout(() => {
          const firstInput = this.$el.querySelector('.el-dialog__body .el-input__inner');
          if (firstInput) {
            firstInput.focus();
          } else {
            const dialogBody = this.$el.querySelector('.el-dialog__body');
            if (dialogBody) {
              dialogBody.setAttribute('tabindex', '-1');
              dialogBody.focus();
            }
          }
        }, 200);
      });
    },
    showUpdateDialog(row) {
      this.updateShow = true;
      this.updatePlaceData = { ...row }
    },
    updatePlaceDataById() {
      if (!this.updatePlaceData.name || this.updatePlaceData.population === null || this.updatePlaceData.population === '' || this.updatePlaceData.area === null || this.updatePlaceData.area === '') {
        this.$message.error("");
        return;
      }
      // 
      const placeData = {
        id: this.updatePlaceData.id,
        name: this.updatePlaceData.name,
        population: parseInt(this.updatePlaceData.population) || 0,
        area: parseFloat(this.updatePlaceData.area) || 0
      };
      updatePlaceInfo(placeData).then(res => {
        if (res.data.code == 200) {
          this.$message.success("")
          this.getPlaceList();
        }
        else {
          this.$message.error(res.data.message || "")
        }
      }).catch(() => {
        this.$message.error("")
      }).finally(() => {
        this.updateShow = false;
      });
    },
    insertPlaceData() {
      if (!this.addPlaceData.name || !this.addPlaceData.population || !this.addPlaceData.area) {
        this.$message.error("");
        return;
      }
      // 
      const placeData = {
        name: this.addPlaceData.name,
        population: parseInt(this.addPlaceData.population) || 0,
        area: parseFloat(this.addPlaceData.area) || 0
      };
      addPlaceInfo(placeData).then(res => {
        if (res.data.code == 200) {
          this.$message.success("")
          this.getPlaceList()
        }
        else {
          this.$message.error(res.data.message || "")
        }
      }).catch(() => {
        this.$message.error("")
      }).finally(() => {
        this.addShow = false;
      });
    },
    deletePlaceInfoById(row) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        deletePlaceInfo({ placeInfoId: row.id }).then(res => {
          if (res.data.code == 200) {
            this.$message.success("")
            this.getPlaceList()
          }
          else {
            this.$message.error(res.data.message || "")
          }
        }).catch(() => {
          this.$message.error("")
        }).finally(() => { });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      })
    },
  }
}
</script>

<style scoped>
.cell {
  text-align: center;
}

.energyleft,
.energyright {
  display: inline-block;
}

.energyright {
  vertical-align: top !important;
  margin-bottom: 19px;
}

.el-table__body tr:hover>td {
  background-color: #e8f5e3 !important;
}

.form-item-inline {
  font-size: 13px;
}

.el-table__fixed {
  height: 100% !important;
}

.main-box-admin {
  width: 94%;
  margin: 20px auto 0;
  text-align: left;
  padding: 5px; /* 3%5px */
}

.flex-box-header-new {
  margin-left: 20px;
  margin-right: 15px;
  margin-top: 15px;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  justify-content: start;
}
</style>




