<template>
  <div class="beijing">
    <button
      @click="denglu()"
      style="
        font-size: 23px;
        border-style: none;
        background: rgba(0, 0, 0, 0);
        margin-right: 80px;
      "
    ></button>
    <div class="box-card">
      <el-card class="form_container">
        <template #header>
          <div class="card-title"></div>
        </template>
        <el-form ref="user" :model="user" :rules="rules">
          <el-form-item prop="username" style="margin-bottom: 5%">
            <el-input
              v-model="user.username"
              placeholder=""
              type="text"
            />
          </el-form-item>
          <el-form-item prop="password" style="margin-bottom: 5%">
            <el-input
              v-model="user.password"
              placeholder=""
              type="password"
              @keyup.enter.native="onSubmit('user')"
            />
          </el-form-item>
          <el-form-item>
            <el-button
              class="button-long"
              type="primary"
              @click="onSubmit()"
              :loading="loading"
              round
            >
              
            </el-button>
          </el-form-item>
        </el-form>
      </el-card>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { login } from "../api/user";
import background from "../pic/background.png";
import tokenManager from "../utils/tokenManager";

//
import pubsub from "pubsub-js";

export default {
  name: "Login",
  data() {
    return {
      loginForm: false,
      user: {
        username: "user",
        password: "123456",
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
        ],
        password: [{ required: true, message: "", trigger: "blur" }],
      },
      loading: false,
      isSubmitting: false,
    };
  },

  created() {
    CSSLayerBlockRule;
    document.onkeydown = (e) => {
      e = window.event || e;

      let that = this;
      if (this.$route.path === "/Tan/TanLogin" && e.keyCode === 13 && !this.isSubmitting && !this.loading) {
        this.onSubmit();
      }
    };
  },
  beforeDestroy() {
    pubsub.unsubscribe(this.pubId);
  },
  methods: {
    denglu() {
      this.loginForm = true;
    },
    onSubmit() {
      if (this.isSubmitting || this.loading) {
        return;
      }
      
      this.isSubmitting = true;
      this.loading = true;
      
      login({
        username: this.user.username,
        password: this.user.password
      })
        .then((res) => {
          if (res.data.code == 200) {
            let type = res.data.data.type;
            localStorage.setItem("id", res.data.data.id);
            
            const { token, refreshToken, expiresIn } = res.data.data;
            tokenManager.setTokens(token, refreshToken, expiresIn);
            
            this.$message({
              message: "" + res.data.data.name + ", ",
              type: "success",
            });

            localStorage.setItem("auth", "admin");
            if (this.$route.params.curRoute == null) {
              this.$router.push({ path: "/Tan/TanPage" });
            } else {
              this.$router.push({ path: this.$route.params.curRoute });
            }

            this.$bus.$emit("updateLoginStatus", "admin");
          } else {
            // descriptionmessage
            const errorMsg = res.data.description || res.data.message || '';
            this.$message({
              message: errorMsg,
            });
            this.loading = false;
            this.isSubmitting = false;
          }
        })
        .catch(() => {
          this.loading = false;
          this.isSubmitting = false;
        })
        .finally(() => {
        });
    },
  },
};
</script>

<style lang="css" scoped>
div {
  margin: 0;
  padding: 0;
}

.beijing {
  width: 100%;
  min-height: calc(100vh - 80px); /*  */
  height: auto; /*  auto */
  padding-bottom: 40px; /*  */

  background: url(../assets/bg.jpg);
  background-size: cover; /*  cover */
  background-position: center; /*  */
  background-repeat: no-repeat; /*  */
}

.form_container {
  height: auto; /*  auto */
  min-height: 265px; /*  */
  position: relative; /*  relative */
  margin: 80px auto 40px; /*  */
  background: #fff;
  width: 430px;
  max-width: 90%; /*  */
  border-radius: 5px;
  padding: 45px;
  text-align: center;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1); /*  */
}
</style>



